'use client'
import { useState, useRef, useEffect } from "react";
import { DashboardLayout } from "@/components/dashboard/dashboard-layout";
import { DashboardHeader } from "@/components/dashboard/dashboard-header";
import { Button } from "@/components/ui/button";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Card, CardContent, CardFooter } from "@/components/ui/card";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Textarea } from "@/components/ui/textarea";
import { Input } from "@/components/ui/input";
import { useSearchParams, useRouter } from "next/navigation";
import { 
  ThumbsUp, 
  MessageSquare, 
  Share2, 
  MapPin, 
  Plus, 
  X, 
  Loader2,
  AlertCircle,
  Tag
} from "lucide-react";
import { useToast } from "@/components/ui/use-toast";
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import { useMutation, useQuery } from '@apollo/client';
import FileUploader, { FileUploaderRef } from "@/components/uploads/fileuploadser";
import MediaDisplay from "@/components/uploads/mediadisplay";
import { Badge } from "@/components/ui/badge";

// Import GraphQL queries and mutations
import { 
  GET_ALL_POSTS,
  GET_MY_POSTS,
  GET_SAVED_POSTS,
  CREATE_POST,
  LIKE_POST
} from '@/app/apollo/mutations/postmutations';
import { GET_PRESIGNED_URL, COMPLETE_FILE_UPLOAD } from '@/app/apollo/mutations/uploadmutations';
import ProtectedRoute from "@/components/ProtectedRoute";

export default function PostsPage() {
  // Route parameter handling
  const searchParams = useSearchParams();
  const router = useRouter();
  const roleParam = searchParams.get('role');
  const [userRole, setUserRole] = useState('student'); // Default role
  
  // State management
  const [postContent, setPostContent] = useState("");
  const [location, setLocation] = useState("");
  const [showLocationInput, setShowLocationInput] = useState(false);
  const [activeTab, setActiveTab] = useState("all");
  const [isUploading, setIsUploading] = useState(false);
  const [uploadError, setUploadError] = useState<string | null>(null);
  const [mediaLinks, setMediaLinks] = useState<string[]>([]);
  const [tags, setTags] = useState<string[]>([]);
  const [tagInput, setTagInput] = useState("");
  const [showTagInput, setShowTagInput] = useState(false);
  const [resetUploader, setResetUploader] = useState(0);
  const fileUploaderRef = useRef<FileUploaderRef>(null);
  const { toast } = useToast();
  
  // Update role when URL parameter changes
  useEffect(() => {
    // Validate the role parameter
    if (roleParam && ['student', 'university', 'company'].includes(roleParam)) {
      setUserRole(roleParam);
    } else if (roleParam) {
      // If role is invalid, redirect to a valid role (student by default)
      router.replace(`/posts?role=student`);
    } else {
      // If no role provided, add it to URL
      router.replace(`/posts?role=student`);
    }
  }, [roleParam, router]);
  
  // Hardcoded user ID for testing
  const hardcodedUserId = "c1605f95-07fa-495d-ab01-fd749dae41dc";
  
  // GraphQL hooks
  const [getPresignedUrl] = useMutation(GET_PRESIGNED_URL);
  const [completeFileUpload] = useMutation(COMPLETE_FILE_UPLOAD);
  const [createPost, { loading: isCreatingPost }] = useMutation(CREATE_POST, {
    refetchQueries: [{ query: GET_ALL_POSTS }, { query: GET_MY_POSTS }],
    onError: (error) => {
      console.error("Detailed GraphQL error:", error);
      setUploadError("Failed to create post. Please try again.");
      toast({
        variant: "destructive",
        title: "Error",
        description: "Failed to create your post. Please try again.",
      });
    }
  });
  const [likePost] = useMutation(LIKE_POST);
  
  const { data: allPostsData, loading: allPostsLoading } = useQuery(GET_ALL_POSTS);
  const { data: myPostsData, loading: myPostsLoading } = useQuery(GET_MY_POSTS, {
    variables: { userId: hardcodedUserId },
    skip: !hardcodedUserId
  });
  const { data: savedPostsData, loading: savedPostsLoading } = useQuery(GET_SAVED_POSTS);
  
  // Use real data if available, otherwise use empty arrays
  const posts = allPostsData?.posts || [];
  const myPosts = myPostsData?.postsByUser || [];
  const savedPosts = savedPostsData?.posts || [];
  
  // Handle file upload completion when manually uploading files
  const handleUploadComplete = (fileUrls: string[]) => {
    console.log("Files uploaded manually:", fileUrls);
    setMediaLinks(prevLinks => {
      // Add only new URLs that don't already exist
      const newLinks = fileUrls.filter(url => !prevLinks.includes(url));
      return [...prevLinks, ...newLinks];
    });
  };
  
  // Toggle location input
  const toggleLocationInput = () => {
    setShowLocationInput(!showLocationInput);
    if (showLocationInput) {
      setLocation("");
    }
  };
  
  // Toggle tag input
  const toggleTagInput = () => {
    setShowTagInput(!showTagInput);
    if (showTagInput) {
      setTagInput("");
    }
  };
  
  // Add tag
  const handleAddTag = () => {
    if (tagInput.trim() === "") return;
    
    // Check if tag already exists
    if (!tags.includes(tagInput.trim())) {
      setTags([...tags, tagInput.trim()]);
    }
    
    // Clear input
    setTagInput("");
  };
  
  // Remove tag
  const removeTag = (tagToRemove: string) => {
    setTags(tags.filter(tag => tag !== tagToRemove));
  };
  
  // Handle tag input key press (add tag on Enter)
  const handleTagInputKeyPress = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === 'Enter') {
      e.preventDefault();
      handleAddTag();
    }
  };
  
  // Prepare location data in the correct format if provided
  const prepareLocationData = () => {
    if (!location) return null;
    
    // For simplicity, we're just using a placeholder.
    // In a real app, you might want to geocode the location string
    return {
      type: "Point",
      coordinates: [0, 0] // [longitude, latitude]
    };
  };
  
  // Handle post creation with S3 integration
  const handleCreatePost = async () => {
    if (postContent.trim() === '' && mediaLinks.length === 0) {
      return;
    }
    
    try {
      setIsUploading(true);
      
      // First upload all files to S3 if there are any pending uploads
      let uploadedUrls: string[] = [];
      if (fileUploaderRef.current) {
        try {
          uploadedUrls = await fileUploaderRef.current.uploadAllFiles();
          console.log("Files uploaded successfully:", uploadedUrls);
        } catch (uploadError) {
          console.error("Error uploading files:", uploadError);
          setUploadError("Failed to upload files. Please try again.");
          setIsUploading(false);
          return; // Stop if upload fails
        }
      }
      
      // Combine any previously uploaded files with new uploads
      const allMediaLinks = [...mediaLinks, ...uploadedUrls.filter(url => !mediaLinks.includes(url))];
      
      console.log("Creating post with variables:", {
        userId: hardcodedUserId,
        content: postContent,
        mediaLinks: allMediaLinks,
        tags: tags,
        location: prepareLocationData()
      });
      
      // Create the post with all media links
      const { data } = await createPost({
        variables: {
          userId: hardcodedUserId,
          content: postContent,
          mediaLinks: allMediaLinks,
          tags: tags,
          location: prepareLocationData()
        }
      });
      
      // Show success message
      toast({
        title: "Post created successfully",
        description: "Your post has been published",
      });
      
      // Reset form
      setPostContent("");
      setLocation("");
      setShowLocationInput(false);
      setTags([]);
      setShowTagInput(false);
      setMediaLinks([]);
      
      // Reset file uploader
      setResetUploader(prev => prev + 1);
      
    } catch (error) {
      console.error("Error creating post:", error);
      setUploadError("Failed to create post. Please try again.");
      
      toast({
        variant: "destructive",
        title: "Error",
        description: "Failed to create your post. Please try again.",
      });
    } finally {
      setIsUploading(false);
    }
  };
  
  const handleLikePost = async (postId: string) => {
    try {
      await likePost({
        variables: { 
          postId,
          userId: hardcodedUserId
        }
      });
    } catch (error) {
      console.error("Error liking post:", error);
      toast({
        variant: "destructive",
        title: "Error",
        description: "Failed to like the post. Please try again.",
      });
    }
  };
  
  // Format text for display based on role
  const getRoleSpecificContent = () => {
    const roleName = userRole.charAt(0).toUpperCase() + userRole.slice(1);
    
    // Post type label differs by role
    let postType = "Posts";
    let postPlaceholder = "What's on your mind?";
    
    if (userRole === 'company') {
      postType = "Job Postings";
      postPlaceholder = "Share a job opportunity...";
    } else if (userRole === 'university') {
      postType = "Announcements";
      postPlaceholder = "Share an announcement with students...";
    }
    
    return { roleName, postType, postPlaceholder };
  };
  
  const { roleName, postType, postPlaceholder } = getRoleSpecificContent();
  
  return (
    <ProtectedRoute allowedRoles={['student', 'university', 'company', 'admin']}>
      <DashboardLayout>
        {/* Rest of your component remains the same */}
        {/* Just ensure the FileUploader has the proper ref and props */}
        <FileUploader 
          ref={fileUploaderRef}
          onUploadComplete={handleUploadComplete}
          disabled={isUploading || isCreatingPost}
          resetTrigger={resetUploader}
        />
      </DashboardLayout>
    </ProtectedRoute>
  );
}