'use client';

import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import { useQuery } from '@apollo/client';
import { IS_LOGGED_IN } from '@/app/apollo/operations/auth';

export default function ProtectedRoute({ 
  children, 
  allowedRoles = [],
  fallbackUrl = '/auth/signin'
}: { 
  children: React.ReactNode,
  allowedRoles?: string[],
  fallbackUrl?: string
}) {
  const router = useRouter();
  const { data, loading } = useQuery(IS_LOGGED_IN);
  const [isAuthorized, setIsAuthorized] = useState<boolean | null>(null);

  useEffect(() => {
    // Only make authorization decision after loading completes
    if (!loading) {
      // Check if user is logged in
      if (!data?.isLoggedIn || !data?.currentUser) {
        router.push(fallbackUrl);
        setIsAuthorized(false);
        return;
      }

      // Check role if needed
      if (allowedRoles.length > 0) {
        const userRole = data.currentUser.role.toLowerCase();
        if (!allowedRoles.includes(userRole)) {
          router.push('/unauthorized');
          setIsAuthorized(false);
          return;
        }
      }
      
      setIsAuthorized(true);
    }
  }, [loading, data, router, allowedRoles, fallbackUrl]);

  // Show loading state while checking authorization
  if (loading || isAuthorized === null) {
    return <div className="flex h-screen items-center justify-center">Loading...</div>;
  }

  // Only render children if authorized
  return isAuthorized ? <>{children}</> : null;
}