import { useState, useCallback } from 'react';
import { createFileWithPreview, FileWithPreview } from '@/lib/s3client';

interface UseFileUploadOptions {
  getPresignedUrl: any; // Apollo mutation
  completeFileUpload: any; // Apollo mutation
  onError?: (error: any, file: FileWithPreview) => void;
}

export const useFileUpload = ({
  getPresignedUrl,
  completeFileUpload,
  onError
}: UseFileUploadOptions) => {
  const [files, setFiles] = useState<FileWithPreview[]>([]);
  const [isUploading, setIsUploading] = useState(false);
  
  const addFiles = useCallback(async (newFiles: FileList) => {
    const fileArray = Array.from(newFiles);
    const newFileWithPreviews = await Promise.all(
      fileArray.map(file => createFileWithPreview(file))
    );
    
    setFiles(prev => [...prev, ...newFileWithPreviews]);
  }, []);
  
  const removeFile = useCallback((fileId: string) => {
    setFiles(prev => {
      const fileToRemove = prev.find(file => file.id === fileId);
      if (fileToRemove?.preview) {
        URL.revokeObjectURL(fileToRemove.preview);
      }
      return prev.filter(file => file.id !== fileId);
    });
  }, []);
  
  const clearFiles = useCallback(() => {
    // Revoke all object URLs
    files.forEach(file => {
      if (file.preview) {
        URL.revokeObjectURL(file.preview);
      }
    });
    setFiles([]);
  }, [files]);
  
  const uploadFile = useCallback(async (file: FileWithPreview): Promise<string> => {
    try {
      // Update file status to uploading
      setFiles(prev => prev.map(f => 
        f.id === file.id ? { ...f, status: 'uploading', progress: 10 } : f
      ));
      
      // Step 1: Get presigned URL
      const { data } = await getPresignedUrl({
        variables: {
          filename: file.name,
          contentType: file.type
        }
      });
      
      const { uploadUrl, fileUrl, key } = data.getPresignedUrl;
      
      // Update progress
      setFiles(prev => prev.map(f => 
        f.id === file.id ? { ...f, progress: 30 } : f
      ));
      
      // Step 2: Upload file to S3 using presigned URL
      const uploadResponse = await fetch(uploadUrl, {
        method: 'PUT',
        body: file.file,
        headers: {
          'Content-Type': file.type,
        }
      });
      
      if (!uploadResponse.ok) {
        throw new Error(`Upload failed with status: ${uploadResponse.status}`);
      }
      
      // Update progress
      setFiles(prev => prev.map(f => 
        f.id === file.id ? { ...f, progress: 70 } : f
      ));
      
      // Step 3: Complete the upload (update database)
      const { data: completeData } = await completeFileUpload({
        variables: {
          key,
          originalFilename: file.name,
          fileSize: file.size,
          fileType: file.type,
          fileUrl
        }
      });
      
      // Update file status to success
      setFiles(prev => prev.map(f => f.id === file.id ? { 
        ...f, 
        status: 'success',
        progress: 100,
        url: fileUrl
      } : f));
      
      return fileUrl;
    } catch (error) {
      console.error('Error uploading file:', error);
      
      // Update file status to error
      setFiles(prev => prev.map(f => f.id === file.id ? { ...f, status: 'error' } : f));
      
      if (onError) {
        onError(error, file);
      }
      
      throw error;
    }
  }, [getPresignedUrl, completeFileUpload, onError]);
  
  const uploadAllFiles = useCallback(async (): Promise<string[]> => {
    const filesToUpload = files.filter(f => f.status === 'ready' || f.status === 'error');
    
    if (filesToUpload.length === 0) {
      return files.filter(f => f.status === 'success').map(f => f.url || '');
    }
    
    setIsUploading(true);
    
    try {
      const uploadPromises = filesToUpload.map(file => uploadFile(file));
      const uploadedUrls = await Promise.all(uploadPromises);
      
      // Combine with already uploaded files
      const allUrls = [
        ...files.filter(f => f.status === 'success' && f.url).map(f => f.url || ''),
        ...uploadedUrls
      ];
      
      return allUrls.filter(url => url !== '');
    } finally {
      setIsUploading(false);
    }
  }, [files, uploadFile]);
  
  return {
    files,
    isUploading,
    addFiles,
    removeFile,
    clearFiles,
    uploadFile,
    uploadAllFiles
  };
};