'use client';

import React, { useRef, useImperativeHandle, forwardRef, useEffect } from 'react';
import { ImageIcon } from 'lucide-react';
import { Button } from '@/components/ui/button';
import FilePreview from './filepreview';
import { useFileUpload } from '../hooks/UseFileUploads'; // Add this import
import { useMutation } from '@apollo/client';
import { GET_PRESIGNED_URL, COMPLETE_FILE_UPLOAD } from '@/app/apollo/mutations/uploadmutations'; // Add this import
import { useToast } from '@/components/ui/use-toast';

// Update interface to include uploadAllFiles in ref
export interface FileUploaderRef {
  clearFiles: () => void;
  uploadAllFiles: () => Promise<string[]>; // Add this line
}

export const FileUploader = forwardRef<FileUploaderRef, FileUploaderRef>(({ 
  onUploadComplete}, ref) => {
  const fileInputRef = useRef<HTMLInputElement>(null);
  const { toast } = useToast();
  
  // Add these GraphQL mutations
  const [getPresignedUrlMutation] = useMutation(GET_PRESIGNED_URL);
  const [completeFileUploadMutation] = useMutation(COMPLETE_FILE_UPLOAD);
  
  // Replace your existing file state management with the hook
  const { 
    files, 
    isUploading, 
    addFiles, 
    removeFile, 
    uploadAllFiles,
    clearFiles
  } = useFileUpload({
    getPresignedUrl: getPresignedUrlMutation,
    completeFileUpload: completeFileUploadMutation,
    onError: (error, file) => {
      toast({
        variant: "destructive",
        title: "Upload failed",
        description: `Failed to upload ${file.name}. Please try again.`
      });
    }
  });
  
  // Update the ref to expose uploadAllFiles
  useImperativeHandle(ref, () => ({
    clearFiles: () => {
      clearFiles();
      if (fileInputRef.current) {
        fileInputRef.current.value = '';
      }
    },
    uploadAllFiles: async () => { // Add this function
      return await uploadAllFiles();
    }
  }), [clearFiles, uploadAllFiles]);
  
  // Keep your existing code for resetTrigger and handleFileChange
  
  // Replace your handleUploadClick with this one
  const handleUploadClick = async () => {
    try {
      const fileUrls = await uploadAllFiles();
      
      if (onUploadComplete) {
        onUploadComplete(fileUrls);
      }
      
      toast({
        title: "Files uploaded successfully",
        description: `${fileUrls.length} file(s) uploaded`
      });
    } catch (error) {
      console.error('Error uploading files:', error);
      toast({
        variant: "destructive",
        title: "Upload failed",
        description: "An error occurred while uploading files."
      });
    }
  };
  
  // Keep the rest of your component as is
  // ...
});